<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Resource'}">教学资源</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Files'}">教学课件</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div class="fileTitle">
          <p class="fileP">{{file.courseName}}</p>
          <div class="fileSpanDiv">
              <span class="fileSpan">
                {{file.courseTime}}
              </span>
            <span class="fileSpan">
                {{file.viewTimes}}人浏览
              </span>
          </div>
        </div>
        <div class="fileDiv">
           <iframe :src="pathUrl" width="100%"></iframe>
        </div>
        <div class="fileComment">
          <div class="fileCommentItem">
            <div class="fileCommentTitle">
              我要评论：
            </div>
            <div class="fileCommentForm">
              <el-form ref="form" :model="commentform" label-width="60px">
                <el-form-item label="姓名：">
                  <el-input v-model="commentform.commentName" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="内容：">
                  <el-input type="textarea"
                            v-model="commentform.commentContent"
                            :autosize="{ minRows: 3, maxRows: 6}"
                            maxlength="100"
                            show-word-limit>
                  </el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="uploadComment">发表评论</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class="fileCommentItem">
            <div class="fileCommentTitle">
              评论列表：
            </div>
            <div class="fileComments" v-if="comments.commentVOS && comments.commentVOS.length > 0">
              <ul class="commentUl" v-for="(comment,index) in comments.commentVOS" :key="index">
                <li class="commenterInfo">
                  <span class="commenterName">{{comment.commentName}}：</span>
                  <span class="commenterTime">{{comment.commentTime}}</span>
                </li>
                <li class="comment">
                  {{comment.commentContent}}
                </li>
              </ul>
              <div class="page">
                <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page.sync="pageNum"
                  :page-size="pageSize"
                  :hide-on-single-page="comments.pages < 2"
                  layout="total, prev, pager, next"
                  :total="comments.total">
                </el-pagination>
              </div>
            </div>
            <div class="fileComments emptyComment" v-else>
              快来抢沙发
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {REQFILE,UPLOADCOMMENT} from '../../../../../api/types'
  import axios from 'axios'
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        file:{},             //详细文件
        courseId:'',         //文件资源ID
        pathUrl:'',          //pdf展示地址

        courseUrl:'',        //ppt下载地址

        pageSize:10,         //评论行数
        pageNum:1,           //评论页数
        commentform:{
          commentName:'',      //评论者姓名
          commentContent:'',   //评论内容
        },
        comments:[],         //评论数组
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //分页
      handleCurrentChange(num){
        this.pageNum = num
        this.reqFile(true)
      },

      //接收文件流解决跨域
      getData(url){
        axios.get(url,{
          responseType:'blob',
        })
          .then(res => {
            let blob = new Blob([res.data], {type: "application/vnd.ms-excel"})
            let objectUrl = URL.createObjectURL(blob)
            this.pathUrl = '../../../../../static/pdf/web/viewer.html?file=' + objectUrl
          })
          .catch(err => {
            console.log(err)
          })
      },

      //获取资源文件详情,isGetComment标识是否只是更新评论
      async reqFile(isGetComment = false){
        var {courseId,pageNum,pageSize} = this
        if(courseId && pageNum && pageSize){
          var result = await REQFILE({courseId,pageNum,pageSize})
          if(result && result.status === 0){
            var {courseUrl,commentListVO,coursePdfUrl} = result.data
            if(!isGetComment){
              this.file = result.data
              this.courseUrl = courseUrl
              //用文件流接收文件再显示，处理跨域
              //this.getData(courseUrl)
              this.pathUrl = '../../../../../static/pdf/web/viewer.html?file=' + encodeURIComponent(coursePdfUrl)
            }
            this.comments = commentListVO
          }
        }
      },

      //发表评论
      async uploadComment(){
        var {courseId} = this
        var {commentName,commentContent} = this.commentform
        if(commentName.trim() && commentContent.trim() && courseId){
          var typeId = courseId
          var result = await UPLOADCOMMENT({commentName,commentContent,typeId})
          if(result && result.status === 0){
            this.commentform.commentName = ''
            this.commentform.commentContent = ''
            var message = '发表成功，等待审核'
            this.message(message,'success',1500)
          }
        }else{
          this.message('请输入完整信息','warning',1500)
        }
      },

      //消息提示
      message(message,type,duration){
        this.$message({
          message,
          type,
          duration
        })
      }
    },
    mounted () {
      if(this.$route.query.courseId){
        this.courseId = this.$route.query.courseId
        //更新菜单选中项
        this.choiceMenuId('Files')
        this.choiceTypeMenuId('Files')
        //获取课件资源详情
        this.reqFile()
      }else{
        this.$router.replace({name:'Files'})
      }
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          .fileTitle{
            text-align: center;
            margin: 15px 0;
            .fileP{
              font-size: 18px;
              font-weight: bold;
              color: #333333;
              line-height: 30px;
              margin-bottom: 0;
              margin-top: 10px;
            }
            .fileSpanDiv{
              line-height: 30px;
              border-bottom: 1px solid rgb(233,233,233);
              text-align: center;
              padding-bottom: 10px;
              margin-bottom: 20px;
              .fileSpan{
                display: inline-block;
                width: 100px;
                font-size: 12px;
                color: rgb(100,100,100);
              }
            }
          }
          .fileDiv{
            iframe{
              position:relative;
              min-height: 600px;
            }
          }
          .fileComment{
            .fileCommentItem{
              margin-top:35px;
              .fileCommentTitle{
                font-size: 18px;
                font-weight: bold;
                color: #333333;
                padding-bottom: 15px;
                border-bottom: 1px solid rgb(233,233,233);
              }
              .fileCommentForm{
                margin-top: 30px;
              }
              .fileComments{
                min-height: 200px;
                ul.commentUl{
                  padding-left: 0px;
                  padding-bottom: 20px;
                  border-bottom: 1px solid rgb(233,233,233);
                  margin: 10px 0;
                  li{
                    list-style: none;
                    padding: 0;
                    margin: 0;
                  }
                  .commenterInfo{
                    line-height: 30px;
                    font-size: 14px;
                    padding: 10px 5px;
                    &:before,&:after{
                      content: '';
                      display: table;
                      clear: both;
                    }
                    .commenterName{
                      display: inline-block;
                      float: left;
                    }
                    .commenterTime{
                      display: inline-block;
                      float: right;
                    }
                  }
                  .comment{
                    padding-left: 2em;
                    line-height: 30px;
                    font-size: 14px;
                    color: #333;
                    max-height: 120px;

                    /*最多显示四行内容，多出来的显示为...*/
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 4;
                    -webkit-box-orient: vertical;
                  }
                }
              }
              .emptyComment{
                padding: 30px 0 0;
                line-height: 30px;
                text-align: center;
                font-size: 20px;
                color: rgb(211,211,211);
              }
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
        .content{
          padding-top: 5px;
          .myDetail{
            .fileTitle{
              .fileP{
                font-size: 16px;
                margin-top: 0;
              }
            }
          }
        }
      }
    }
  }
</style>



